.Buy {
    margin-top: 60px;
}
.Buy .images{
    text-align: center;

}
.Buy .title{
    height: 50px;

}
.Buy .title .title-price{
    display: grid;
    grid-template-columns: repeat(6 ,1fr);
}
.Buy .title .title-price :first-child{
    grid-column: span 5;
}
.Buy .title .title-price :last-child {
    grid-column: span 1 ;
}

.Buy .title h3{
    margin-top: 10px;
    margin-left: 30px;
    font-size: 20px;
    font-family: Arial, Helvetica, sans-serif;
    display: inline-block;
}
.Buy .images img {
    width: 250px;
    height: 250px;
    margin: 10px;
    border:1px solid whitesmoke;
    border-radius: 10px;
    text-align: center;
}
.Buy .count button{
    height: 20px;
    width: 20px;
    margin: 10px;
    margin-right: 100px;
}
.Buy .count .countState{
    margin-right: 60px;
}
.Buy .description{
    height: 120px;
    width: 40%;

}
.Buy .paymentCart{
    display: flex;
    align-items: center;
}
.Buy .paymentCart a{
    text-decoration: none;
    font-size: 20px;
    margin: 20px;
    text-align: center;
    background:#F9EBEA;
    border-radius: 30px;
    color: black;
   

}